.wp-block-blocks-gamestore-featured-products {
  margin-top: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem)!important;
  margin-bottom: clamp(3.75rem, calc(2.77rem + 4.92vw), 7.5rem)!important;

  h2 {
    line-height: 1.4;
    background: var(--accent-gradient);
    background-clip: text;
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    margin-top: 0;
    margin-bottom: 24px;
    text-align: center;
    width: fit-content;
    position: relative;
    z-index: 1;
    display: block;
    margin-left: auto;
    margin-right: auto;
  }

  p {
    max-width: 980px;
    margin-left: auto;
    margin-right: auto;
    margin-bottom: 48px;
    position: relative;
    z-index: 1;
    color: var(--text-secondary, rgba(14, 13, 15, 0.70));
    text-align: center;
  }

  .games-list {
    display: flex;
    gap: 16px;
    align-items: flex-start;
    flex-wrap: wrap;

    .game-result {
      max-width: 243px;

      .game-meta h3 {
        color: var(--text-primary, #0E0D0F);
      }
    }
  }
}